
.edge {
    stroke: black;
    stroke-width: 3px;
    stroke-miterlimit: 10;
    fill: none;
    pointer-events: none;
}

.edge_pom {
    stroke-width: 15px;
    fill: none;
    // stroke: #08fc00;
    pointer-events: stroke;
}

.edge_pom:hover {
    cursor: pointer;
}

.edge_point {
    stroke: none;
    cursor: move;
    fill: rgba($color: #077bff, $alpha: 1.0);
}

.add_point {
    fill: rgba($color: #07e2ff, $alpha: 0.7)
}

.add_point:hover {
    fill: rgba($color: #08fc00, $alpha: 0.9);
    cursor: pointer;
}