@import "Editor/Styles/Variables.scss";
@import  "Editor/Styles/Mixins.scss" ;


.loader_layer {
    position:  absolute;
    width: 100%;
    height: 100%;
    top:0px;
    left: 0px;
    background-color: $loader-bg-color;
}

.loader_wrapper {
    margin: 5% 40% 0 40%;
}

.loader_content {
    margin: 0 40%;
    width: 50%;
}

.loader {
    border: 16px solid  $loader-bar-color;
    border-radius: 50%;
    border-top: 16px solid $loader-runner-color;
    width: 250px;
    height: 250px;
    -webkit-animation: spin 2s linear infinite; /* Safari */
    animation: spin 2s linear infinite;
    margin: 0;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* Safari */
@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  